<template>
  <div class="container">
    <SecondTitle :title="secondTitle" @cancel="back"></SecondTitle>
    <div class="contain">
      <img src="../../static/img/image_lamp_accident_details.png">
      <div class="text">
        <p class="textInfo">出现暂时的设备故障，原因：{{textInfo}}</p>
        <ul class="stepInfo">
          <li v-for="(item, index) in stepInfo" :key="index">{{item}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import SecondTitle from './SecondTitle'
import faultDetection from '../common/faultDetection'
import step from '../common/step'
export default {
  components: {
    SecondTitle
  },
  data () {
    return {
      secondTitle: '小狗吸尘器故障',
      textInfo: '',
      stepInfo: [],
      code: 0
    }
  },
  mounted () {
    this.stepInfo = step
    this.code = Number(this.$route.params.status)
    this.getCode()
  },
  methods: {
    back () {
      this.$router.go(-1)
    },
    getCode () {
      this.textInfo = faultDetection[this.code]
    }
  }
}
</script>
<style scoped>
  .container{
    width: 100%;
    height: 100%;
    background: #ffffff;
  }
  .container::before{
    display: table;
    content: '';
  }
  .contain{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  }
  .contain img{
    margin-top: 6.5rem;
    width: 7.8rem;
    height: 7.8rem;
  }
  .text{
    margin-top: 5.8rem;
  }
  .stepInfo{
    margin-top: 1.5rem;
  }
  .stepInfo li{
    margin: 0.6rem 0;
    font-size: 1.2rem;
    font-weight: 300;
  }
  .textInfo{
    font-size: 1.4rem;
    font-weight: 500;
  }
</style>



